
body { padding-bottom: 70px;
}
.bills header {
  text-align: center;
  color: #fff;
  /*border-bottom: 1px solid #ededed;*/
}
.bills header.positive {
  background-color: #1abc9c !important;
}
.bills header.negative {
  background-color: #e74c3c !important;
}
.header-title {
  outline: none;
  position: relative;
  margin: 0 auto;
}
.header-title-inner {
  line-height: 20px;
  padding: 6px 0;
  position: relative;
}
.header-content {
  font-size: 25px;
  padding-top: 20px;
  position: relative;
  margin: 0 auto;
  width: 100%;
}
.bills footer {
  text-align: left;
}
.bills footer label {
  position: relative;
  display: inline-block;
  line-height: 50px;
}
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background-color: #f5f5f5;
}



